            <p>A través del Sitio www.TurnosKlasse.com (en adelante, el ”Sitio y/o Turnos Klasse”), toda persona podrá encontrar diversos servicios y contenidos relacionados la búsqueda de automóviles y servicios relacionados (en adelante, los “Servicios”).</p>
            <p>1. Toda persona que utilice el Sitio (en adelante, “Usuario”) acepta sin condicionamientos todos y cada uno de los presentes Términos y Condiciones de Uso (“Términos de Uso”), sus modificaciones y documentos complementarios y se obliga a cumplirlos sin objeciones. Si Ud. no está de acuerdo con alguno de los Términos de Uso, no utilice este Sitio. Se deja expresamente aclarado que los presentes Términos de Uso tienen carácter obligatorio y vinculante. Es responsabilidad de los Usuarios informarse y/o consultar periódicamente los T&C o Politicas de Privacidad.</p>
