            <p>3. Ciertos contenidos accedidos a través del Sitio pueden estar alcanzados por normas específicas que reglamenten y complementen a los presentes Términos y Condiciones de Uso, razón por la cual se recomienda al Usuario tomar conocimiento específico de ellas.</p>
            <p>4. Condiciones de acceso y utilización del Sitio:</p>
            <p>4.1. La utilización de cualquier Servicio del Sitio atribuye la condición de Usuario del mismo.</p>
            <p>4.2. El acceso y utilización del Sitio no exige la previa suscripción o registro del Usuario. Sin perjuicio de ello, la utilización de algunos Servicios que se ofrecen a través del Sitio requiere el registro del Usuario.</p>
            <p>4.3. A fin de registrarse en el Sitio, el Usuario deberá completar el Formulario de Registración con su información personal, indicando además una dirección de correo electrónico y una contraseña. Será responsabilidad del usuario no compartir sus contraseñas.</p>
            <p>4.4. Uso del Sitio y sus Servicios. El Usuario reconoce y acepta que el uso de los contenidos y/o Servicios ofrecidos por el Sitio será bajo su exclusivo riesgo y/o responsabilidad. El Usuario se compromete a utilizar el Sitio y todo su contenido y Servicios de conformidad con la ley, la moral, el orden público, los presentes Términos de Uso, en su caso, le sean de aplicación. Asimismo, se compromete hacer un uso adecuado de los Servicios y/o contenidos del Sitio y a no emplearlos para realizar actividades ilícitas o constitutivas de delito, que atenten contra los derechos de terceros y/o que infrinjan la regulación sobre propiedad intelectual e industrial, o cualesquiera otras normas del ordenamiento jurídico aplicable. En particular, el Usuario se compromete a no trasmitir, introducir, difundir y poner a disposición de terceros, cualquier tipo de material e información (datos contenidos, mensajes, dibujos, archivos de sonido e imagen, fotografías, software, etc.) que sean contrarios a la ley, la moral, el orden público, los presentes Términos de Uso y, en su caso, a las Condiciones Particulares que le sean de aplicación. A título enunciativo, y en ningún caso limitativo o excluyente, el Usuario se compromete a:</p>
            <p>4.4.1. No ingresar o difundir contenidos o propaganda de carácter racista, xenófobo, pornográfico, de apología del terrorismo o que atenten contra los derechos humanos.</p>
            <p>4.4.2. No ingresar o difundir en la red programas de datos (virus y software nocivo) susceptibles de provocar daños en los sistemas informáticos del proveedor de acceso, sus proveedores o terceros usuarios de la red Internet.</p>
            <p>4.4.3. No ingresar, difundir, transmitir o poner a disposición de terceros cualquier tipo de información, elemento o contenido que constituya publicidad ilícita o desleal.</p>
            <p>4.4.4. No transmitir publicidad no solicitada o no autorizada, material publicitario, "correo basura", "cartas en cadena", "estructuras piramidales", o cualquier otra forma de solicitación, excepto en aquellas áreas (tales como espacios comerciales) que hayan sido exclusivamente concebidas para ello.</p>
            <p>4.4.5. No ingresar o difundir cualquier información y contenidos falsos, ambiguos, extemporáneos o inexactos de forma que induzca a error a los receptores de la información.</p>
