            <p>4.4.9. No ingresar, difundir, transmitir o poner a disposición de terceros cualquier tipo de información, elemento o contenido que suponga una violación del secreto de las comunicaciones y la legislación de datos de carácter personal.</p>
            <p>4.4.10. No dirigirse a los usuarios mediante el envío de Spam, o directamente ofrecer, publicar o crear, una base de datos personales de terceros.</p>
