            <p>4.4.14. No intentar descifrar, descompilar u obtener el código fuente de cualquier programa de software que comprenda o constituya una parte de este Sitio.</p>
            <p>5. Servicios en particular: Se deja aclarado que el Sitio cuenta, incluyendo pero no limitando, con los siguientes Servicios:</p>
            <p>5.1. Búsqueda de automóviles, navegación y favoritos: Para buscar un automóvil el Usuario podrá utilizar el buscador que se encuentra en la página principal del Sitio. Los filtros responden a las características de los avisos que se muestran en el resultado. Al seleccionar un filtro, el resultado se actualizará automáticamente. El Usuario puede seleccionar cuantos filtros desee de esa forma y así encontrar los automóviles que mejor se ajusten a sus necesidades.</p>
            <p>5.1.2.  Un Auto Para vos, EstoyCubierto y Garantía Mecánica. Turnos Klasse proporcionará los datos necesarios  a Sector Seguro para hacer llegar propuestas y cotizaciones de seguros. En el caso de Garantía Mecáncia, la cobertura y garantía estará abalada por la compañia de seguros, no siendo un servicio directo de Turnos Klasse. Turnos Klasse no brinda garantía sobre los vehículos publicados. Un Auto para vos te brinda la posibilidad de marcar los filtros que te lleven a tu próximo vehículo según tus gustos y prioridades. </p>
            <p>5.2. Intermediación de datos. Turnos Klasse proporcionará a Sector Seguro los datos ingresados por los Usuarios para realizar cotizaciones en seguros, para proporcionar al usuario costos del mercado, para que los usuarios puedan contratar su seguro a través del Broker de Seguros.</p>
            <p>5.2. Alertas: Las Alertas permiten que el Usuario reciba en su casilla de correo electrónico, las novedades de los Automóviles que cumplan con sus criterios guardados. De esta forma, le simplificamos el proceso de búsqueda enviando alertas automáticas al momento de publicación. Logrando dar con oportunidades antes de que otros las busquen en Turnos Klasse. </p>
            <p>5.3. Guardar Favoritos: Este servicio permite al Usuario guardar todos aquellos avisos que resulten de su interés. Para guardar el Usuario un aviso en sus favoritos, simplemente tiene que hacer clic en la estrella. Si el aviso fuese desactivado, desaparecerá automáticamente de su lista. Lo mismo si el Usuario lo elimina del mismo. Se deja aclarado que el Usuario deberá estar registrado para utilizar el Servicio de Alertas.</p>
            <p>5.4 Buscar o contactar a un vendedor. Para contactar al Vendedor de un aviso, el Usuario debe hacer clic en el aviso, el Usuario deberá podrá escribir o ver directaemente los datos en pantalla. Podrán intercambiar mails o mensajes por inbox. Se deja expresamente aclarado que, los datos del Usuario y del Vendedor sólo podrán ser utilizados a efectos de concluir la operación originada en el Sitio o de publicaciones similares a la original, las cuales seran seleccionadas por el Sitio y no deberá ser empleada por ninguno de ellos con fines publicitarios o promocionales u otras actividades no relacionadas con el Sitio, salvo la expresa autorización del Usuario o del Vendedor al efecto. Por ello, el Usuario y los Vendedores declaran y garantizan que mantendrán indemne al Sitio por cualquier reclamo judicial y/o extrajudicial, en tal sentido. Será responsabilidad del comprador chequear que la operación sea segura, Turnos Klasse no es responsable por el mal uso que se pueda llevar a cabo a través de la plataforma. </p>
            <p>5.5. Recomendar avisos. Para recomendar un aviso, el Usuario debe seleccionar la opción Enviar, que se encuentra dentro del mismo. En el formulario que se desplegará, el Usuario debe ingresar dirección de correo electrónico de su amigo, el Sitio enviará un e-mail con la descripción del aviso a la casilla del correo electrónico de su amigo. También se pondrá recomendar vía whatsapp. Estas funciones están en proceso de desarrollo. </p>
            <p>5.6. Publicación Online: Para publicar un aviso, el Usuario debe estar registrado en el Sitio. Si ya se encuentras registrado, puede ingresar desde la página principal del Sitio colocando su E-mail y contraseña. Luego, desde Publicar un aviso, deberá completar el formulario de publicación . La duración es de 60 días según aviso. Toda la información se encuentra detallada en www.TurnosKlasse.com </p>
            <p>6. Al registrarse y/o navegar el Sitio, el Usuario y/o el visitante brinda información personal, prestando su consentimiento para que la misma sea almacenada directamente en nuestra base de datos, encontrándose protegida electrónicamente, utilizando los mecanismos de seguridad informática de protección de la información más completos y eficaces para mantenerla en total confidencialidad. Podrán solicitar la eliminación de su información de la base de datos sin ningún inconveniente enviando un mail a contacto@TurnosKlasse.com con el asunto "Datos Personales" y allí mismo indicar su información completa. Toda la información de la base de datos, será procesada por el Broker de Seguros de Sector Seguro, para hacerle llegar a los usuarios las cotizaciones de seguros.</p>
            <p>7. El Usuario es responsable por el buen uso del Sitio, comprometiéndose expresamente a evitar cualquier tipo de acción que pueda dañar a sistemas, equipos o servicios accesibles directa o indirectamente a través de Internet, incluyendo la congestión intencional de enlaces o sistemas y de acuerdo a los presentes Términos y Condiciones de Uso.</p>
            <p>8. Se deja expresamente aclarado que el Sitio podrá ser utilizado única y exclusivamente por personas mayores de 18 años, debiendo abstenerse de utilizarlo las personas que no sean mayores de dicha edad.</p>
            <p>9. Toda la información acerca de los automóviles publicados y/u ofrecidos por el Sitio es proporcionada por Usuarios, siendo responsabilidad exclusiva de los mismos dicha información como así también lo referente a su comercialización. De igual forma, al momento de compra venta , la responsabilidad es exclusivamente de las partes que llevan a cabo la operación. Para ello, le recomendamos leer atentamente las preguntas frecuentes para reducir las posibilidades de fraude. </p>
            <p>10. TurnosKlasse.com  no se responsabiliza por las publicaciones, veracidad o exactitud de la información presentada en el sitio. Ni de los cambios que sobre las mismas publicaciones pudieran acontecer.  Turnos Klasse puede eliminar cualquier publicación en cualquier momento, suspender a un usuario o prohibirle el acceso si fuera necesario. </p>
            <p>11. Se deja aclarado expresamente que el Sitio no revisa el contenido de los Avisos, proveyendo única y exclusivamente un espacio para su publicación. En tal sentido, el Usuario mantendrá indemne a Turnos Klasse. </p>
            <p>12. Turnos Klasse podrá patrocinar o vender espacios publicitarios dentro de las publicaciones o en sus buscadores sin otorgar ninguna responsabilidad sobre las publicaciones mismas. El Usuario es responsable por el buen uso de del servicio, comprometiéndose expresamente a evitar cualquier tipo de acción que pueda dañar a sistemas, equipos o servicios accesibles directa o indirectamente a través de Internet, incluyendo la congestión intencional de enlaces o sistemas y de acuerdo a los presentes Términos y Condiciones.</p>
            <p>13. El Usuario asume plena responsabilidad frente a Turnos Klasse y a terceros por daños y perjuicios que se produjeran como consecuencia del accionar propio, de sus dependientes o de terceros conectados a través del Usuario y los que resulten de la inobservancia de las leyes o reglamentaciones de otros hechos ilícitos o del mal uso que se haga del servicio, debiendo el Usuario indemnizar y mantener indemne a TurnosKlasse.com ante cualquier reclamo de pago que pudiera corresponder.</p>
            <p>14. TurnosKlasse.com  tendrá el derecho de ampliar o mejorar los servicios prestados sin necesidad de comunicación previa, así como de alterar los medios o rutas utilizados para la prestación del servicio. </p>
            <p>15. La falta de ejercicio temporario por parte de Turnos Klasse de los derechos que los presentes le confieren, no implicarán una renuncia a los mismos, los cuales podrán ser ejercidos en todo momento.</p>
