<template>
  <section class="hero">
    <div class="has-background-white">
      <div class="container has-text-left">
        <h2 class="title">
          <span>Sucursales</span>
        </h2>
      </div>
    </div>
    <div class="hero-body">
      <div v-if="dealers && dealers.length" class="container dealers">
        <div class="columns">
          <div class="column has-text-left">
            <h1 class="has-text-info">Conozca nuestras sucursales</h1>
          </div>
        </div>
        <div class="columns is-multiline">
          <div v-for="dealer in dealers" class="column is-3">
            <router-link class="dealer" :to="'/dealers/'+ dealer.slug">
              <div class="bg-inline" :style="'background-image:url(' + dealer.imagen + ')'">
              </div>
              <div class="has-text-left">
                <h1 class="has-text-info" v-html="dealer.nombre"></h1>
                <h6 class="has-text-dark" v-html="dealer.telefono"></h6>
                <h6 class="has-text-dark" v-html="dealer.direccion"></h6>
              </div>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>

import axios from 'axios';
import moment from 'moment';
import helper from '@/components/Helper';

export default {
  name: 'Dealers',
  mounted: function(){
    if(!this.dealers.length) {
      var that = this
      axios.get( `/static/dummy/mercedes-dealers.json`)
        .then(res => {
          that.dealers = res.data;
        }).catch(function(error) {
          console.log(error)
        })      
    }
  },
  data () {
    return {
      dealers:[],
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>



