export default  {
	props:['adultos','chicos','bebes'],
	data: function() {
    	return {
        	mutableAdultos: this.adultos,
        	mutableChicos: this.chicos,
        	mutableBebes: this.bebes
        }
   	},	
	methods: {
	    // a computed getter
	    increment: function (what) {
			if(this[what] < 99){
	      		this[what]++;
	      	}
	    },
	    decrement: function (what) {
			if(this[what] > 0){
				this[what]--;
			}
	    }
	},
    watch: {
        mutableAdultos: {
            handler: function() {
              	this.$emit('passengerchanged', {adultos:this.mutableAdultos,chicos:this.mutableChicos,bebes:this.mutableBebes});
          	},
           	deep: true
        },
        mutableChicos: {
            handler: function() {
              	this.$emit('passengerchanged', {adultos:this.mutableAdultos,chicos:this.mutableChicos,bebes:this.mutableBebes});
          	},
           	deep: true
        },
        mutableBebes: {
            handler: function() {
              	this.$emit('passengerchanged', {adultos:this.mutableAdultos,chicos:this.mutableChicos,bebes:this.mutableBebes});
          	},
           	deep: true
        }
    },
	template: `
		<div class="passengerpicker">
			<div class="row">
				<span class="lab">Adultos</span>
				<span class="rem bg-inline" @click="decrement('mutableAdultos')"></span>
				<span class="val">{{ mutableAdultos }}</span>
				<span class="add bg-inline" @click="increment('mutableAdultos')"></span>
			</div>
			<div class="row">
				<span class="lab">Chicos</span>
				<span class="rem bg-inline" @click="decrement('mutableChicos')"></span>
				<span class="val">{{ mutableChicos }}</span>
				<span class="add bg-inline" @click="increment('mutableChicos')"></span>
			</div>
			<div class="row">
				<span class="lab">Bebés</span>
				<span class="rem bg-inline" @click="decrement('mutableBebes')"></span>
				<span class="val">{{ mutableBebes }}</span>
				<span class="add bg-inline" @click="increment('mutableBebes')"></span>
			</div>
		</div>
		`
}


