<template>
  <section class="hero">
    <div class="has-background-white">
      <div class="container has-text-left">
        <h2 class="title">
          <span v-html="dealer.nombre"></span>
        </h2>
      </div>
    </div>
    <div class="hero-body">
      <div class="container has-text-left">
        <!--pre v-html="dealer"></pre-->
        <p><strong>Dirección:</strong> <span v-html="dealer.direccion"></span></p>
        <p><strong>Teléfono:</strong> <span v-html="dealer.telefono"></span></p>
        <p jsb-word="terms-text"></p>
      </div>
    </div>
  </section>
</template>

<script>

import axios from 'axios';
import moment from 'moment';
import helper from '@/components/Helper';

export default {
  name: 'Dealer',
  mounted: function(){
    if(!this.dealer.length) {
      var that = this
      axios.get( `/static/dummy/mercedes-dealers.json`)
        .then(res => {
          _.map(res.data, function(value, key) {
            if(value.slug === that.$route.params.slug){
              console.log(value)
              console.log(JSON.parse(JSON.stringify(value)))
              //self.dealer = JSON.parse(JSON.stringify(value))
              that.dealer = value
            }
          });
        }).catch(function(error) {
          console.log(error)
        })      
    }
  },
  data () {
    return {
      dealer:[],
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>



